// add styles for this prototype
.access-container {
    --border-color: #CCCCCC;
    --background-color: #fff;
    --background-color-light: #F8F6F3;
    --va-base-bg: white;
    // --btn-bg-primary: #9EFF00;
    // --btn-bg-primary-hover: #83D400;
    // --btn-bg-primary-focus: #83D400;
    // --btn-text-primary: black;
    --va-form-border-active: var(--border-color);
    --btn-text-link: var(--va-teal-400);
    --btn-text-link-hover: var(--va-teal-400);
    // --btn-text-link-active: var(--va-grey-700);
    // --btn-text-link-focus: var(--va-grey-700);

    --font-light: "Proxima Nova W01 Light","Arial",sans-serif;
    --font-medium: "Proxima Nova W01 Medium","Arial",sans-serif;
    --font-regular: "Proxima Nova W01 Regular","Arial",sans-serif;
    --font-semibold: "Proxima Nova W01 Semibold","Arial",sans-serif;
    --font-thin: "Proxima Nova W01 Thin","Arial",sans-serif;

    background-color: white;
    // padding-top: 78px;
    min-height: 100vh;
    // padding-bottom: 2rem;
    

    // .va-nav-bar {
    //     position: fixed;
    //     width: 100vw;
    //     z-index: 1000;
    //     top: 0;
    // }
    .va-btn-link {
        white-space: nowrap;
    }
    .nowrap {
        white-space: nowrap;
    }
    .tier-label {
        text-transform: uppercase;
    }
    .img-container {
        max-width: 1440px;
    }
    .login-container {
        padding-top: 12rem;
        display: flex;
        justify-content: center;
        width: 260px;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        .login-title {
            font-size: 1.5rem;
            text-align: center;
            padding: 2rem 0;
        }
        .login-options {
            width: 100%;
            margin-bottom: 1rem;
        }
    }
    .launchpad-header {
        color: var(--va-grey-500);
        padding: 1rem 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: 2rem;
    }
    .launchpad-container {
        padding: 0 2rem 3rem;
    }
    .launchpad-tiles {
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
        max-width: 65.5rem;
        margin: 0 auto;
        margin-bottom: 2rem;
        .launchpad-tiles-row1 {
            display: flex;
            gap: 2rem;
            margin-bottom: 2rem;
            flex-wrap: wrap;
        }
        .launchpad-tiles-row2 {
            display: flex;
            gap: 2rem;
            flex-wrap: wrap;
        }
        .va-tile {
            max-width: 238px;
            max-height: 238px;
            display: flex;
            flex-direction: column;
            padding: 0;
            padding-top: 0.5rem;
            padding-bottom: 3.5rem;
            box-shadow: 1px 1px 2px 0 rgba(17, 17, 17,  0.1);
            border: 1px solid var(--va-grey-300);
            min-width: 190px;
            .va-signal-icon {
                margin: 1.5rem 0 0 1.5rem;
            }
            .va-tile-content {
                margin-left: 1.5rem;
                margin-right: 1.5rem;
                margin-top: 0.625rem;
                width: auto;
            }
            .va-tile-description {
                font-size: 0.875rem;
                line-height: 1.2;
            }
            .launch {
                bottom: 0;
                opacity: 0;
                position: absolute;
                transition: all 350ms ease;
                right: 25px;
                visibility: hidden;
            }
            .arrow-icon {
                display: inline-block;
                margin-top: 36px;
                margin-left: 10px;
                height: 33px;
                right: 0;
                transform: translateY(-50%);
                vertical-align: middle;
                width: 43px; 
                .bit {
                    position: absolute;
                    transition: all 350ms ease;
                }
                .tl {
                    top: 0;
                    left: 0;
                }
                .tr {
                    top: 0;
                    right: 0;
                }
                .bl {
                    bottom: 0;
                    left: 0;
                }
                .br {
                    bottom: 0;
                    right: 0;
                }
                .arrow {
                    opacity: 0;
                    visibility: hidden;
                    transform: translate(-5px, 0px);
                }
                
            }
            &:hover {
                background-color: white;
                box-shadow: 0 2px 4px 0 var(--va-cyan-500);
                .launch {
                    visibility: visible;
                    opacity: 1;
                }
                .arrow-icon {
                    .tl {
                        transform: translate(-10px, -10px);
                    }
                    .tr {
                        transform: translate(10px, -10px);
                    }
                    .bl {
                        transform: translate(-10px, 10px);
                    }
                    .br {
                        transform: translate(10px, 10px);
                    }
                    .arrow {
                        opacity: 1;
                        transform: translate(0px, 0px);
                        visibility: inherit;
                    }
                }
            }
        }
    }
    .va-tile-wide {
        display: flex;
        background-color: var(--va-grey-800);
        color: white;
        gap: 2rem;
        justify-content: space-between;
        padding: 0.5rem 0;
        max-width: 65.5rem;
        margin: 0 auto  ;
        box-shadow: 1px 1px 2px 0 rgba(17, 17, 17,  0.1);
        border: 1px solid var(--va-grey-300);
        
        .va-tile-left {
            display: flex;
            // flex-direction: column;
            // width: 45%;
            .va-signal-icon {
                margin: 0 0 0 1.5rem;
            }
            .va-tile-content {
                margin-left: 1.5rem;
                margin-right: 1.5rem;
                margin-top: 0.625rem;
                width: auto;
                padding: 1rem 0 1.5rem;
                color: white;
                .va-tile-title {
                    font-size: var(--va-tile-title-font-size);
                    color: white;
                }
            }
            .va-tile-description {
                font-size: 0.875rem;
                line-height: 1.2;
                color: white;
            }
        }
        .va-tile-right {
            // background-color: var(--va-grey-200);
            // border-left: 1px solid var(--va-grey-300);
            // flex-grow: 2;
            display: flex;
            align-items: center;
            padding: 0 2rem;
            gap: 1rem;
            min-width: 40%;
            .va-btn {
                min-width: 80px;
            }
            
            &.va-tile-right-single {
                min-width: auto;

                .advertiser-selection-right {
                    padding-top: 0;
                    position: relative;
                    .single-advertiser-label {
                        position: absolute;
                        right: 0;
                        top: -1.25rem;
                        opacity: 1;
                        // transition: 0.5s ease;
                    }
                    // &:hover {
                    //     .single-advertiser-label {
                    //         right: 0;
                    //         opacity: 1;
                    //         transition: 0.5s ease;
                    //     }
                    // }
                }
                .va-btn {
                    min-width: 200px;
                }
            }
        }

        .va-form-label {
            color: white;
        }
        .va-select:not(.va-select--link) .va-select__control {
            background-color: var(--va-grey-700);
            border-color: var(--va-grey-800);
        }
        &:hover {
            background-color: var(--va-grey-800);
            border: 1px solid var(--va-grey-300);
            box-shadow: 1px 1px 2px 0 rgba(17, 17, 17,  0.1);
            color: white;
        }
        &.light {
            background: var(--va-grey-200);
            border-color: var(--va-grey-400);
            color: var(--va-grey-900);
            .va-tile-content {
                color: var(--va-grey-900);
                .va-tile-title {
                    color: var(--va-grey-900);
                }
            }
            .va-tile-description, .va-form-label {
                color: var(--va-grey-900);
            }
            .va-select:not(.va-select--link) .va-select__control {
                background-color: var(--va-grey-100);
                border-color: var(--va-grey-400);
            }
        }
        &.white {
            background: white;
            border-color: var(--va-grey-300);
            color: var(--va-grey-900);
            .va-tile-content {
                color: var(--va-grey-900);
                .va-tile-title {
                    color: var(--va-grey-900);
                }
            }
            .va-tile-description, .va-form-label {
                color: var(--va-grey-900);
            }
            .va-select:not(.va-select--link) .va-select__control {
                background-color: var(--va-grey-100);
                border-color: var(--va-grey-400);
            }
        }
    }
    // .advertiser-selection-left, .advertiser-selection-right {
    //     border: 1px solid red;
    // }
    .advertiser-selection-left {
        flex-grow: 2;
        width: 70%;
    }
    .advertiser-selection-right {
        flex-grow: 1;
    }

    .all-data-create-btn.va-btn.va-btn-dropdown {
        padding-left: 2rem;
        padding-right: 2rem;
        .va-btn-dropdown-indicator {
            display: none;
        }
    }
    .dashboard-container {
        background-color: transparent;
    }
    .page-content {
        padding: 0;
        flex-grow: 2;
    }
    .nav-bar {
        background-color: var(--background-color);
        position: relative;
        width: 224px;
        flex-grow: 0;
        z-index: 910;
        display: flex;
        align-items: center;
        flex-direction: column;
        border-right: 1px solid var(--border-color);
        transition: 0.5s ease;

        &.nav-bar--collapsed {
            width: 68px;
            .nav-item {
                width: 40px;
            }
        }

        .nav-items-container {
            padding-top: 62px;
            position: sticky;
            // top: 78px;
            top: 0;
        }
   
        .nav-item {
            color: #888888;
            width: 192px;
            height: 40px;
            border-radius: 4px;
            margin-bottom: 1rem;
            padding: 0 0.5rem;
            display: flex;
            align-items: center;
            overflow: hidden;
            transition: 0.5s ease;
            cursor: pointer;
            &:hover {
                background-color: white;
                .nav-item-label {
                    color: #111;
                }
            }
            
            .va-signal-icon {
                margin-right: 0.5rem;
            }
            .nav-item-label {
                font-size: 0.75rem;
                white-space: nowrap;
                color: #666;
            }
            .nav-item-content {
                display: flex; 
                align-items: center;
            }
            
        }
        .nav-item-selected {
            .nav-item {
                background-color: #181A18;
                color: white;
                .nav-item-label {
                    color: white;
                }
            }
            
        }
        .collapse-button-container {
            position: absolute;
            right: -28px;
            top: 20px;
        }
        .collapse-icon {
            opacity: 0;
            background: white;
            padding: 0;
            border-radius: 0.25rem;
            transition: 0.25s ease;
            border: 1px solid #ccc;
            &.va-btn-icon:hover .va-signal-icon {
                background-color: transparent;
            }
            .va-signal-icon {
                transform: rotate(90deg);
                transition: 0.25s ease;
            }
            &.icon-expand {
                .va-signal-icon {
                    transform: rotate(270deg);
                    transition: 0.25s ease;
                }
            }
        }
        &:hover {
            .collapse-icon {
                display: block;
                opacity: 1;
                transition: 0.25s ease;
                &:hover {
                    border-color: #888;
                }
            }
            
        }
        &.nav-bar-option2 { 
            .collapse-button-container {
                position: relative;
                right: auto;
                left: 194px;
                top: -16px;
                transition: left 0.5s ease;
                &.collapse-button-container-expand {
                    left: 40px;
                    transition: left 0.5s ease;
                }
            }
        }
        &.nav-bar-option3 { 
            .collapse-button-container {
                top: auto;
                position: fixed;
                left: 212px;
                bottom: 32px;
                width: 26px;
                transition: left 0.5s ease;
                &.collapse-button-container-expand {
                    left: 55px;
                    transition: left 0.5s ease;
                }
            }
        }
    }
    
    .content-header {
        border-bottom: 1px solid var(--va-grey-300);
        background-color: var(--background-color);
        padding: 1rem 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 160px;
        transition: 0.5s ease;
        position: sticky;
        width: 100%;
        // top: 78px;
        top: 0;
        z-index: 900;
        h1 {
            font-size: 3.25rem;
            font-weight: 300;
            letter-spacing: -0.8px;
            transition: 0.5s ease;
        }
        .content-header-label {
            color: #777;
            font-size: 0.75rem;
            font-weight: 700;
            text-transform: uppercase;
            transition: 0.5s ease;
        }
        .va-btn-secondary {
            background-color: white;
            color: black;
            font-size: 1rem;
            height: auto;
            max-height: 3rem;
            &:hover {
                background-color: var(--va-grey-200);
            }
        }
        .va-select {
            border-radius: 4px;
            padding: 4px;
            .va-form-label {
                color: #777;
                font-size: 0.625rem;
                font-weight: 700;
            }
            &.va-select--link-large .va-select__single-value {
                font-size: 36px;
                font-weight: 700;
            }
            &.va-select--link {
                .va-select__control {
                    height: auto;
                }
                .va-select__value-container {
                    color: #111;
                    font-weight: 300;
                    font-size: 24px;
                } 
                .va-select__dropdown-indicator .va-signal-icon {
                    background-color: white;
                    height: 28px;
                    position: relative;
                    top: 4px;
                    border-radius: 4px;
                    border: 1px solid var(--border-color);
                    width: 32px;
                    height: 32px;
                    margin-left: 20px;
                    svg {
                        width: 18px;
                        color: black;
                        position: absolute;
                        top: 6px;
                        left: -2px;
                    }
                }
                .va-select__control, .va-select__control--is-focused, .va-select__control--menu-is-open {
                    background: transparent !important;
                    .va-select__single-value {
                        color: #111;
                        text-decoration: none;
                    }
                    &:hover, &:focus {
                        .va-select__single-value {
                            color: #111;
                            text-decoration: none;
                        }
                    }
                }
                
            }
            &:hover {
                background: transparent;
                .va-select__value-container {
                    color: #111;
                    &:hover {
                        color: #111;
                    }
                } 
            }
        }
        .content-header-right {
            display: flex;
            align-items: center;
            gap: 88px;
            transition: 0.5s ease;
        }
        .header-action-button {
            height: 80px;
            width: 60px;
            text-align: left;
            font-size: 0.75rem;
            font-weight: 500;
            position: relative;
            display: flex;
            align-items: center;
            padding-left: 0;
            .va-signal-icon {
                position: absolute;
                left: 0;
                top: 0;
                color: #888;
                transition: 0.25s ease;
            }
            .button-line {
                position: absolute;
                left: 0;
                bottom: 0;
                width: 22px;
                height: 1px;
                background-color: black;
                opacity: 0.15;
                transition: 0.25s ease;
                &::after {
                    content: '';
                    // transform: rotate(-45deg);
                    position: absolute;
                    right: 0;
                    top: -4.5px;
                    width: 0; 
                    height: 0; 
                    border: 0;
                }
            }
            &:hover {
                .va-signal-icon {
                    color: black;
                }
                .button-line {
                    width: 60px;
                    opacity: 1;
                    transition: 0.25s ease;
                    &::after {
                        border-top: 5px solid transparent;
                        border-bottom: 5px solid transparent;
                        border-left: 5px solid black;
                    }
                }
            }
            &:focus {
                border: 0;
                outline: none;
            }
        }
    }
    &.scrolling {
        .content-header {
            height: 80px;
            transition: 0.5s ease;
            h1 {
                font-size: 2rem;
                transition: 0.5s ease;
            }
            .content-header-label {
                font-size: 0.625rem;
                transition: 0.5s ease;
            }
        }
        .content-header-right {
            gap: 64px;
            transition: 0.5s ease;
        }
        .header-action-button {
            height: 80px;
            width: 86px;
            padding-left: 24px;
            transition: 0.5s ease;
            .va-signal-icon { 
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                transition: 0.5s ease;
            }
            .button-line { 
                bottom: 16px;
                left: 24px;
            }
        }
        .filter-buttons-container { 
            border-bottom: 1px solid var(--va-grey-300);
            box-shadow: 0 5px 2px -2px rgba(0, 0, 0, .05);
        }
    }
    .create-select-button {
